  }

  // @media (max-width:768px) {
  //   padding: 10px 40px;
  // }

  @media (max-width:430px) {
    padding: 12px 25px;
  }
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;

  &.active {
    opacity: 1;
    visibility: visible;

    .menu-overlay {
      transform: translateX(0);
    }
  }
}

.menu-overlay {
  position: absolute;
  right: 0;
  top: 0;
  width: 85%;
  max-width: 320px;
  height: 100%;
  background: #ffffff;
  box-shadow: -5px 0 25px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;

  @media (max-width: 480px) {
    width: 100%;
    max-width: 100%;
  }
}

.menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem;
  margin-bottom: 1.5rem;

  @media screen and (max-width: 1024px) {
    padding: 2.4rem 0rem;
  }
  @media screen and (max-width: 576px) {
    padding:0rem 10px;
  }

  .close-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 50%;
    transition: background-color 0.2s;

    &:hover {
      background-color: #f5f5f5;
    }

    .close-icon {
      width: 24px;
      height: 24px;
      color: #333;
    }
  }
}

.nav-menu {
  display: flex;
  flex-direction: column;
  list-style: none;
  gap: 2rem;
  margin-top: 2rem;
  @media screen and (max-width: 1024px) {
    padding: 0;
    margin-top: 1rem;
    gap: 1rem;
  }
}

.nav-item {
  .nav-link {
    display: flex;
    align-items: center;
    padding: 0.75rem 1rem;
    text-decoration: none;
    border-radius: 8px;
    gap: 1rem;
    transition: all 0.2s ease;

    &:hover {
      background-color: #f3f3f3;
      color: #000;
    }
  }
}

.nav-divider {
  height: 1px;
  background-color: #f0f0f0;
  margin: 1rem 0;
}

.nav-button {
  display: block;
  margin-top: 1rem;
  text-decoration: none;
}

.menu-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;

  .social-links {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1rem;

    .social-link {
      color: #1D1D1D;
      transition: color 0.2s;

      &:hover {
        color: #11191F;
      }

      lucide-icon {
        width: 20px;
        height: 20px;
      }
    }
  }

}

// Responsive adjustments

.size16 {
  font-size: 16px;

  @media (max-width: 1600px) {
    font-size: 14px;
  }

}